<template>
    <div class='xinwen'>
        <div class="logo">
            <div class="left">
                <img src="../assets/img/shangcheng/logo.png" alt="">
            </div>
            <div class="right">
                <!-- <div class="sousuo2">
                    <div>
                        <el-input
                            placeholder="请输入内容"
                            v-model="input"
                            clearable>
                        </el-input>
                    </div>
                    <div class="sousuosousuo" @click="sousuo()">
                        搜索
                    </div>
                </div>
                <div class="cen">
                    <div class="list">
                        <div>热门搜索</div>
                        <div class="item" v-for="(item,i) in hot_searches" :key="i" @click="sousuo(item)">{{item}}</div>
                    </div>
                </div> -->
            </div>
        </div>
        <div class="title">新闻列表</div>
        <div class="cen" v-html="get_news_info.content"></div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            get_news_info:'',
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.apis.get(_this.apis.get_news_info,{params:{lang:_this.apis.lang,id:_this.$route.query.id}}).then(function(res) {
            if(res.data.code == 0 ){
                _this.get_news_info = res.data.data
            }
        })
    },
    watch:{
    },
    methods:{
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.xinwen{
    .logo{
        width: 1360px;
        height: 137px;
        margin: 0 auto;
        padding-top: 20px;
        display: flex;
        justify-content: space-between;
        align-items: top;
        .left{
            width: 444px;
            height: 87px;
        }
    }
    .title{
        font-size: 18px;
        font-weight: bold;
        color: #fff;
        height: 44px;
        line-height: 44px;
        margin: 20px 0;
        text-align: center;
        background: #FFAD01;
    }
    .cen{
        width: 1200px;
        margin: 0 auto;
        margin-bottom: 80px;
    }
}
</style>


